.Navigation {
    height: 56px;
    width: 100%;
    color: whitesmoke;
    background-color: #1E1E1E;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 90;
}
.Navigation ul {
    list-style: none;
}

.Navigation ul, .Navigation ul li {
    height: 100%;
    margin: 0;
}

.Wrapper {
    max-width: 768px;
    width: 100%;
    margin: 0 auto;
}


